@mixin svgWhite() {
  filter: invert(1) sepia(0) saturate(0) hue-rotate(0) brightness(1);
}
@mixin svgJadeite() {
  filter: invert(92%) sepia(83%) saturate(173%) hue-rotate(71deg) brightness(91%) contrast(89%);
}
@mixin svgWatermelon() {
  filter: invert(63%) sepia(39%) saturate(3960%) hue-rotate(321deg) brightness(98%) contrast(88%);
}
@mixin svgTeal() {
  filter: invert(50%) sepia(65%) saturate(322%) hue-rotate(138deg) brightness(85%) contrast(90%);
}

@mixin bg-opacity($color, $opacity: 0.3) {
    background: $color; /* The Fallback */
    background: rgba($color, $opacity); 
}
